<template>
    <div style="height:100%;"  class="bgColor">
        <Header></Header>
         <v-dialog    v-model="dialog"  width="500" height="400">
      <v-card>
        <v-card-title class="text-h5 primary lighten-2">
          请输入问卷标题
        </v-card-title>
        <v-card-text>
            <v-text-field
            label="输入标题" v-model="title"
            solo
            dense
            style="width: 15vw;  margin-top: 6vh;"
          ></v-text-field>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary"  text  @click="jumpTo">
            确认
          </v-btn>
          <v-btn color="primary"  text  @click="Cancel">
            取消
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
        <div style="font-size:30px;text-align:center;padding:30px;font-family:'bgtitlefamily';margin-top:2vh;">问卷类型</div>
        <div style="text-align: center;margin: 0 100px;margin-top:2vh;">
            <v-row>
                <v-col @click="ToSurvey" style="margin-left:10vw;">
                    <QuestionnareCard class="cardClass">
                        <img src="@/assets/questionnare/调查.png" alt="" slot="picture">
                        <div slot="character" >调查</div>
                    </QuestionnareCard>
                </v-col>
                <v-col @click="toExam">
                    <QuestionnareCard class="cardClass">
                        <img src="@/assets/questionnare/考试.png" alt="" slot="picture">
                        <div slot="character">考试</div>
                    </QuestionnareCard>
                </v-col>
                <v-col @click="toVote" style="margin-right:10vw;">
                    <QuestionnareCard class="cardClass" >
                        <img src="@/assets/questionnare/投票.png" alt="" slot="picture" >
                        <div slot="character" >投票</div>
                    </QuestionnareCard>
                </v-col>
            </v-row>
            <v-row >
                <v-col @click="toSignUp" style="margin-left:10vw;">
                    <QuestionnareCard class="cardClass">
                        <img src="@/assets/questionnare/报名.png" alt="" slot="picture">
                        <div slot="character">报名</div>
                    </QuestionnareCard>
                </v-col>
                <v-col @click="toClockIn">
                <QuestionnareCard class="cardClass">
                  <img src="@/assets/questionnare/报名.png" alt="" slot="picture">
                  <div slot="character">打卡</div>
                </QuestionnareCard>
              </v-col>
                <v-col  style="margin-right:10vw;">
                    <QuestionnareCard class="cardClass">
                        <img src="@/assets/questionnare/paper.png" alt="" slot="picture" style="margin-top:8vh;">
                        <div slot="character">敬请期待</div>
                    </QuestionnareCard>
                </v-col>
            </v-row>
        </div>
    </div>
</template>

<script>
import "@/assets/font/font.css"
import Header from '@/components/Header'
import QuestionnareCard from '@/components/QuestionnareCard'
export default {
    name: 'Questionnare',
    data(){
        return {
            dialog:false,
            title:'',
            type:-1
        }
    },
    components: {
        QuestionnareCard,
        Header,
    },
    methods: {
      ToSurvey(){
        this.dialog=true
        this.type=0
      },
      toExam(){
        this.dialog=true
        this.type=1
      },
      toVote(){
        this.dialog=true
        this.type=2
      },
      toSignUp(){
        this.dialog=true
        this.type=3
      },
      toClockIn(){
        this.dialog=true;
        this.type=4;
      },
      Cancel(){
        this.dialog = false;
      },
      jumpTo(){
          if(this.title=='') {
              this.$message.warning('请输入标题')
          }
          else{
            if(this.type==0) this.$router.push({path:'/surveyquestionnare/'+-1, query:{ title: this.title, type: "0" }})
            else if(this.type==1) this.$router.push({path:'/exam/questionnare/'+-1, query:{ title: this.title }});
            else if(this.type==2) this.$router.push({path:'/vote/questionnare/'+-1, query:{ title: this.title }});
            else if(this.type==3) this.$router.push({path:'/signup/questionnare/'+-1, query:{ title: this.title }});
            else if(this.type==4) this.$router.push({path:'/clockinquestionnare/'+-1, query:{ title: this.title, type: "4" }});
            this.dialog=false
            this.title=''
          }
      }
    }
}
</script>

<style scoped>
.bgColor {
    background-color: rgb(247, 247, 247);
}
.cardClass {
    margin: 0 15px;
}
.cardClass:hover {
    box-shadow: 4px 4px 10px 4px rgba(0, 0, 0, .3);
}
</style>